.App {
  text-align: center;
  padding-top: 50px;

  position: relative;
  /* overflow: hidden; */
}

/* Поисковый инпут */
.search-input {
  position: relative;
  bottom: 10px;

  width: 400px;
  height: 30px;
}

.table {
  width: 800px;
  height: 700px;
  margin-left: auto;
  margin-right: auto;
}

.table-head {
  display: flex;
}

.table-head-capture {
  width: 400px;
  border: 1px solid black;
  background-color: grey;
  transition: all 0.4s ease;
}

.table-head-capture:hover {
  background-color: rgb(83, 77, 77);
  color: honeydew;
  cursor: pointer;
}

.table-body {
  overflow-y: auto;
}

.table-row {
  display: flex;
}

.table-cell-in-row {
  width: 400px;
  border: 1px solid black;
  /* display: inline-block; */
}

.pagination {
  position: absolute;
  left: 117px;
  top: 24px;
  display: flex;
  margin-right: auto;
  margin-left: auto;

  max-width: 400px;
}

.pagination-item {
  width: 40px;
  height: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: grey;
  transition: all 0.3s ease;
}

.pagination-item:hover {
  cursor: pointer;
  background-color: rgb(58, 56, 56);
  color: white;
}

.pagination-item:not(:last-child) {
  margin-right: 4px;
}

.active-pagination-item {
  background-color: rgb(58, 56, 56);
  color: white;
}

.table-row {
  border: 1px solid black;
}
